<template>
	<!-- type01 -->
	<div class="pl-5 pr-5">
		<!-- type01 -->
		<div>
			<h2>Type01</h2>
			<v-carousel class="mt-5">
				<v-carousel-item
					src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"
					cover
				></v-carousel-item>

				<v-carousel-item
					src="https://cdn.vuetifyjs.com/images/cards/hotel.jpg"
					cover
				></v-carousel-item>

				<v-carousel-item
					src="https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"
					cover
				></v-carousel-item>
			</v-carousel>
		</div>

		<!-- type02 -->
		<div class="mt-10">
			<h2>Type02</h2>
			<v-carousel
				height="400"
				hide-delimiters
				progress="primary"
				class="mt-5"
			>
				<v-carousel-item
					v-for="(slide, i) in slides"
					:key="i"
				>
					<v-sheet
						height="100%"
					>
						<div class="d-flex fill-height justify-center align-center">
							<div class="text-h2">
								{{ slide }} slide
							</div>
						</div>
					</v-sheet>
				</v-carousel-item>
			</v-carousel>
		</div>

		<!-- type03 -->
		<div class="pt-10 pb-10">
			<h2>Type03</h2>
			<v-sheet
				class="mx-auto"
				max-width="600"
			>
				<v-slide-group
					show-arrows
				>
					<v-slide-group-item
						v-for="n in 25"
						:key="n"
						v-slot="{ isSelected, toggle }"
					>
						<v-btn
							class="ma-2"
							rounded
							:color="isSelected ? 'primary' : undefined"
							@click="toggle"
						>
							Options {{ n }}
						</v-btn>
					</v-slide-group-item>
				</v-slide-group>
			</v-sheet>
		</div>

		<!-- type04 -->
		<div class="pt-10 pb-10">
			<h2>Type04</h2>
			<v-stepper :items="['Step 1', 'Step 2', 'Step 3']" class="mt-5">
				<template v-slot:[`item.1`]>
					<v-card title="step1 conts" flat>step11111111</v-card>
				</template>
				<template v-slot[`item.2`]>
					<v-card title="step2 conts" flat>step22222222</v-card>
				</template>
				<!-- <template v-slot[`item.3`]>
					<v-card title="step3 conts" flat>step333333333</v-card>
				</template> -->
			</v-stepper>

			<v-progress-circular
				indeterminate
				color="primary"
			></v-progress-circular>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				slides: [
					'first',
					'second',
					'third',
					'fourth',
					'fifth'
				]
			}
		}
	}
</script>